import React from 'react'

const Work = () => (
  <div>
    <div
      style={{
        margin: '0 auto',
        maxWidth: 960,
        paddingLeft: '0.5rem',
        background: 'rebeccapurple',
        marginBottom: '0.5rem',
      }}
    >
      <h1 style={{ margin: 0, color: `white`, paddingBottom: '0.05rem' }}>
        What I Do
      </h1>
    </div>
    <p>
      <h4>OSM - Networks and routing</h4>
      <p>
        Checking where people come from and go to relies on semi-accurate
        routing networks, Open street maps has this and the benefit of being
        free.
      </p>
      <h5>Bonus: Graphs are cool!</h5>
    </p>

    <p>
      <h4>Modelling - Automation</h4>
      <p>
        Automating away aspects of modelling allows planners and modellers to
        focus more on the problem and less on implementing solutions into their
        modelling software
      </p>
      <h5>Reformatting data, Cleaning data, Visualisations</h5>
    </p>
  </div>
)

export default Work



