import React from 'react'
import Link from 'gatsby-link'
import aboutcontent from '../content/about'

const AboutMe = () => (
  <div>
    <div
      style={{
        margin: '0 auto',
        maxWidth: 960,
        paddingLeft: '0.5rem',
        background: 'rebeccapurple',
        marginBottom: '0.5rem',
      }}
    >
      <h1 style={{ margin: 0, color: `white`, paddingBottom: '0.05rem' }}>
        About Me
      </h1>
    </div>
    <span style={{ margin: 0 }}>{aboutcontent}</span>
  </div>
)

export default AboutMe



